.App {
    text-align: center;
  }
  
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
    height: 80px;
  }
  
  .App-header {
    background-color: #222;
    height: 150px;
    padding: 20px;
    color: white;
  }
  
  .App-title {
    font-size: 1.5em;
  }
  
  .App-intro {
    font-size: large;
  }
  
  @keyframes App-logo-spin {
    from { transform: rotate(0deg); }
    to { transform: rotate(360deg); }
  }
  
  .bg-center-img {
      border-style:none;
      border-width:0;
      background-size:contain;
      background-position: center; 
      background-repeat: no-repeat;
  }
  
  .jk-cart {
    display: flex;
    align-items: center;
  }
  .jk-cart>div {
    position: relative;
  }
  .jk-cart>div>u {
    position: absolute;
    right: -0.5rem;
    top: 0rem;
    font-size: 0.7em;
    background-color: red;
    display: flex;
    align-items: center;
    color: white;
    min-width: 1rem;
    padding: 0 3px;
    height: 1.0rem;
    text-align: right;
    text-decoration: none;
    justify-content: center;
    border-radius: 0.6rem;
  }
  
  .text-danger-1 {
    color: #ffc0c0;
  }
  .text-danger-2 {
    color: #ff8080;
  }
  .text-danger-3 {
    color: #ff4040;
  }
  
  .sep-product-select {
    border-top: 1px solid #f0f0f0;
  }
  
  .point-man-top {
    background: linear-gradient(#17b8b880,#17a2b880, #176ab880);
  }
  
  .point-remarks {
    list-style: decimal;
    padding-left: 1rem;
    padding-bottom: 2rem;
  }
  
  .point-remarks>li {
    margin-bottom: 1rem;
  }
  
  .noneMassage {
     height: 50px;
  }
  
  .author {color: #99f}